import React from 'react';
import {
  BaseComponent,
  View,
  Controller,
} from '../../components';

class App extends BaseComponent {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div id="app" style={{height:"100%",display: "flex",flexDirection:"column",justifyContent:"center",alignItems: "center"}}>
        <div style={{height:"80%",width:"95%",backgroundColor:"rgb(3,54,73)",alignItems: "center"}}>
          <View/>
        </div>
        <div  style={{height:"10%",width:"95%",backgroundColor:"rgb(3,101,100)"}}>
          <Controller/>
        </div>
      </div>
    );
  }
}

export default App;



