import React from 'react';
import { connect } from 'react-redux';
import { BaseComponent } from '../../components';
import { actions } from '../../reducers';
import {Tree} from '../../core/';
import {test} from './code.js';

class View extends BaseComponent {
  constructor(props) {
    super(props);
    this.randomPointsNumber=null;
    this.steinerPointsNumber=null;
    this.operations= [];
    this.root = null;
    this.oldProgress=-1;
    this.objects={};
    this.set(this.props.player.randomPointsNumber,this.props.player.steinerPointsNumber);
  }

  set(randomPointsNumber,steinerPointsNumber){
    this.randomPointsNumber=randomPointsNumber;
    this.steinerPointsNumber=steinerPointsNumber;
    this.operations= test(this.randomPointsNumber,this.steinerPointsNumber);
    this.props.setTotal(this.operations.length);
  }

  componentWillReceiveProps(nextProps) {
    if(this.randomPointsNumber!=nextProps.player.randomPointsNumber||this.steinerPointsNumber!=nextProps.player.steinerPointsNumber){
      this.set(nextProps.player.randomPointsNumber,nextProps.player.steinerPointsNumber);
      this.oldProgress=-1;
    }

    var newProgress=nextProps.player.progress;
    if(this.oldProgress==-1){
      this.objects['Tree'] = new Tree("Tree","Tree");
    }
    if(this.oldProgress+1<=newProgress){
      this.applyOperations(this.operations.slice(this.oldProgress+1,newProgress+1));
      if (newProgress==(this.operations.length-1)){
        this.oldProgress=-1;
      }else{this.oldProgress=newProgress}

    }
    else{
      this.objects['Tree'] = new Tree("Tree","Tree");
      this.applyOperations(this.operations.slice(0,newProgress+1));
    }
  }

  applyOperations(operations){
    operations.forEach(steps => this.applySteps(steps));
  }

  applyCommand(command) {
    const { methodName, args } = command;
    this.objects["Tree"][methodName](...args);
  }

  applySteps(steps) {
    steps.forEach(command => this.applyCommand(command));
  }


  render() {
    return (
      <div  id="View" style={{height:"100%",width:"100%",display:"flex" ,justifyContent : "center",alignItems : "center"}}>
        {
          [this.objects['Tree']].map(tracer => tracer && tracer.render())
        }
      </div>
    );
  }
}

export default connect(({ player }) => ({ player }), actions)(View);



